<template lang="pug">
v-data-table.small-table(:items="tdm.proxy.tracks", :headers="headers", :rows-per-page-items="rowsPerPage")
  template(slot="items", slot-scope="props")
    tr(:disabled="props.item.state !== 'active'", @click="$emit('click', props.item)")
      td(v-for="m in meta", :key="m") {{ props.item.meta[m] }}
      td [{{ props.item.begin }}, {{ props.item.end}}]
      td
        v-icon(:style="{ color: props.item.color }") brightness_1
</template>

<script>
export default {
  props: {
    tdm: {
      type: Object,
      required: true,
    },
    meta: {
      type: Array,
      default:() => [],
    },
  },
  data() {
    return {
      rowsPerPage: [15, 25],
      headers: [].concat(
        ...this.meta.map(m => ({
          text: m,
          value: `meta.${m}`,
          width: "1%",
        })),
        ...[{
          text: 'Range',
          value: 'begin',
          width: "1%",
        },
        {
          text: 'Color',
          value: 'color',
          width: "1%",
        }],
      ),
    };
  },
};
</script>

<style lang="stylus">
.small-table {
  th, td {
    padding: 0 12px !important;
  }
  tr[disabled] {
    background-color: rgb(193, 193, 193);
  }
}
</style>
