<script>
const buttonEnum = {
  GOOD: "Good",
  INVALID: "Invalid_Input",
  NOT_TURKABLE: "Not_Turkable",
  RESET: "Reset"
};

export default {
  props: {
    activityId: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      buttons: buttonEnum,
      activeButton: buttonEnum.RESET,
      base_id: null,
      error: '',
    };
  },
  methods: {
    async loadState() {
      const { data } = await this.axios.get(`activity/${this.activityId}`);
      this.activeButton = data.audit_status || buttonEnum.RESET;
      this.base_id = data.grozper_activity_id;
    },
    audit(value) {
      this.error = '';
      if (!this.base_id) {
        this.error = 'Grozper Activity ID Missing';
      }
      let payload;
      let endpoint = 'audit';
      switch(value){
        case buttonEnum.GOOD:
          payload = {
            status: value,
            good: true,
          };
          break;
        case buttonEnum.INVALID:
          payload = {
            status: value,
            good: false,
          }
          break;
        case buttonEnum.NOT_TURKABLE:
          payload = {
            status: value,
            good: false,
          };
          break;
        case buttonEnum.RESET:
          payload = {
            state: 'Gladiator_Bad',
          }
          endpoint = 'reset'
          break;
      }
      this.axios.put(`activity/${this.base_id}/${endpoint}`, payload).then(() => {
        // this.activeButton = value;
        this.loadState();
      });
    },
  },
  mounted() {
    this.loadState();
  }
};
</script>

<template lang="pug">
v-layout(row, wrap)
  v-btn(
      color="success",
      depressed,
      :outline="activeButton !== buttons.GOOD",
      @click="audit(buttons.GOOD)") Good
  v-btn(
      color="error",
      depressed,
      :outline="activeButton !== buttons.INVALID",
      @click="audit(buttons.INVALID)") Invalid
  v-btn(
      color="warning",
      depressed,
      :outline="activeButton !== buttons.NOT_TURKABLE",
      @click="audit(buttons.NOT_TURKABLE)") Not Turkable
  v-btn(
      color="info",
      depressed,
      :outline="activeButton !== buttons.RESET",
      @click="audit(buttons.RESET)") Reset
</template>
