<script>
import {
  HIT_TYPE,
  KWIT_TYPE,
} from 'stumpf-common/utils/api.js';

export default {
  props: {
    api: {
      type: Object,
      required: true,
    },
    enabled: {
      type: Boolean,
      required: true,
    },
    submitPayload: {
      required: true,
    },
    submitTo: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      dialog: false,
    };
  },
  computed: {
    stringSubmitPayload() {
      return JSON.stringify(this.submitPayload)
    },
  },
  methods: {
    async finish() {
      if (!this.enabled) return;
      try {
        await this.api.submit(this.submitPayload);
        await this.api.log({
          type: 'finish',
          context: this.api.context,
          answer: this.submitPayload,
        });
      } finally {
        if (!this.api.development && this.api.taskType.toLowerCase() === 'hit') {
          this.$refs.mturkForm.submit();
        }
        this.dialog = false;
      }
    },
    open() {
      this.dialog = true;
    },
  }
};
</script>

<template lang="pug">
v-dialog(
    width="500px",
    v-model="dialog",
    :disabled="!enabled"
    @keydown.esc="dialog = false",
    @keydown.enter.stop="finish")
  v-btn.pa-1(slot="activator", :disabled="!enabled", large, color="primary") Submit
  v-card
    form(
        ref="mturkForm",
        name="mturk_submit",
        method="post",
        :action="submitTo")
      input(
          type="hidden",
          name="assignmentId",
          :value="api.assignmentId")
      input(
          type="hidden",
          name="answer",
          :value="stringSubmitPayload")
      
      v-card-title.grey.lighten-3.py-4.title(dark, primary-title) Submit task
      
      v-card-text
        slot(name="summary")

      v-card-actions.pa-3
        v-spacer
        v-btn(@click="dialog = false") Cancel (Esc)
        v-btn(type="submit", @click.prevent="finish", color="primary", tabindex="0") Finish (Enter)
</template>
