<template lang="pug">
v-layout(row, wrap, align-center, justify-start)
  v-flex(shrink, v-for="(v, k) in stateMap", :key="k",)
    v-checkbox.ma-0.px-1.py-2(
      :input-value="v === STATES.ACTIVE",
      hide-details,
      :color="colorMap[k]",
      :label="k",
      :value="v === STATES.ACTIVE"
      @change="updateState(k, v)")
  v-btn.ma-0.ml-2(v-if="stateMap", icon, color="primary", @click="$emit('update:stateMap', resetState(tdm, colorBy))")
    v-icon refresh
</template>

<script>
import { colorByMeta, toggleByMeta, resetState, STATES } from 'stumpf-common/utils/tdm';
export default {
  props: {
    colorBy: {
      type: String,
      requried: true,
    },
    colorMap: {
      required: true,
    },
    tdm: {
      type: Object,
      required: true,
    },
    stateMap: {
      requred: true,
    },
  },
  data() {
    return {
      checked: [],
      STATES,
    };
  },
  methods: {
    colorByMeta,
    resetState,
    updateState(key, currentState) {
      const newState = currentState === STATES.DISABLED
        ? STATES.ACTIVE
        : STATES.DISABLED;
      const sm = toggleByMeta(this.tdm, this.colorBy, key, newState);
      this.$emit('update:stateMap', sm);
      this.$emit('changed')
    },
  },
}
</script>
