<template lang="pug">
.track-app
  v-container(fluid)
    v-layout(row, wrap, justify-center, ref="container")
      v-flex.mb-2
        v-layout.mb-2(column, align-center)
          v-flex(shrink, :style="{ 'width': width + 'px'}")
            slot(name="title")
            video-region(
              ref="region",
              :url="url",
              :tdm="tdm",
              :videoWidth="videoWidth",
              :videoHeight="videoHeight",
              :width="width",
              :label="showLabels",
              :label-format="labelFormat",
              :shapes="showShapes",
              :line-width="3",
              :time="activetime",
              :framerate="framerate",
              :loading="loading || loadingInternal",
              :playing.sync="playing",
              :playbackRate="playbackRate",
              @timecurrent="passivetime = $event"
              @init="$emit('init')")
            video-controls(
              :playing.sync="playing",
              @skip="$refs.region.skip($event)",
              :color-by="colorBy",
              @update:colorBy="$emit('update:colorBy', $event)"
              :color-by-options="meta",
              :show-labels.sync="showLabels",
              :show-shapes.sync="showShapes",
              :progress="passivetime",
              :offset="offset",
              @update:progress="activetime = $event",
              :playback-rate.sync="playbackRate",
              :length="duration")
      v-flex
        v-card.mb-2
          tdm-table(:tdm="tdm", :meta="meta", @click="handleTrackSelect")
        slot(name="widget")
</template>

<script>
import VideoRegion from './VideoRegion.vue';
import VideoControls from './VideoControls.vue';
import TdmTable from './TdmTable.vue';
import { getParams } from '../utils';
import { colorByMeta, disableByMeta, resetState } from '../utils/tdm';

export default {
  components: {
    TdmTable,
    VideoControls,
    VideoRegion,
  },
  props: {
    tdm: {
      type: Object,
      required: true,
    },
    url: {
      type: String,
      required: true,
    },
    duration: {
      /* in seconds */
      type: Number,
      required: true,
    },
    offset: {
      type: Number,
      default: 0,
    },
    framerate: {
      type: Number,
      default: 30,
    },
    videoWidth: {
      type: Number,
      required: true,
    },
    videoHeight: {
      type: Number,
      required: true,
    },
    autoplay: {
      type: Boolean,
      default: true,
    },
    loading: {
      type: Boolean,
      default: false,
    },
    labelFormat: {
      type: Array,
      default: () => [],
    },
    colorBy: {
      type: String,
      default: '',
    },
    meta: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      activetime: 0,
      passivetime: 0,
      showLabels: true,
      showShapes: true,
      width: 0,
      annotations: null,
      loadingInternal: false,
      playing: this.autoplay,
      playbackRate: 1.0,
    };
  },
  watch: {
    offset(newval) {
      this.activetime = newval;
      this.passivetime = newval;
    },

  },
  methods: {
    handleTrackSelect(track) {
      // Hack to force activeTime to a different unique value.
      this.activetime = (track.begin / this.framerate) + Math.random() / 100;
      this.passivetime = this.activetime;
      this.$emit('trackselect', track);
    },
    resize() {
      // TODO: something better than this.
      const minWidth = 900;
      const tw = 620;
      const cw = this.$refs.container.clientWidth;
      if (tw + minWidth < cw) {
        this.width = cw - tw - 20;
      } else {
        this.width = cw;
      }
    },
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.resize);
  },
  mounted() {
    this.resize();
    window.addEventListener('resize', this.resize);
    /* Get parameters from query string */
    const params = getParams(window.location.hash.substring(1));
    if ('colorBy' in params) {
      this.$emit('update:colorBy', params['colorBy']);
    }
    if ('select' in params) {
      this.$emit('select', params['select']);
    }
  },
};
</script>
