<template lang="pug">
v-toolbar.video-controls(shift dense)
  v-layout(row, align-center, wrap)
    v-flex(shrink)
      v-btn(icon, @click='$emit("skip", false)')
        v-icon skip_previous
      v-btn(icon, @click='$emit("update:playing", !playing)')
        v-icon(v-if='playing') pause
        v-icon(v-else) play_arrow
      v-btn(icon, @click='$emit("skip", true)')
        v-icon skip_next
      span.px-1 {{ toTimeStamp(progress) }} / {{ toTimeStamp(offset + length) }}
    v-flex(grow)
      v-slider.px-2(
          @end="progressChangeEnd",
          @input="progressChangeInput",
          @start="progressChangeStart",
          @change="progressChangeEnd",
          ref="progress",
          :value="progress - offset",
          hide-details,
          :max="length",
          :step=".1")
    v-layout(shrink, row, align-center, space-between)
      v-flex
        v-select.px-2(
            @change="$emit('update:playbackRate', $event)",
            :items="playbackRateOptions",
            :value="playbackRate",
            solo)
          template(slot="item", slot-scope="props")
            p {{ `${props.item}x Speed` }}
          template(slot="selection", slot-scope="props")
            span {{ `${props.item}x Speed`}}
      v-flex
        v-switch.px-2.flat(
            label="labels",
            hide-details,
            :input-value="showLabels",
            @change="$emit('update:showLabels', $event)")
      v-flex
        v-switch.px-2.flat(
            label="shapes",
            hide-details,
            :input-value="showShapes",
            @change="$emit('update:showShapes', $event)")
      v-flex
        v-select.px-2(
            @change="$emit('update:colorBy', $event)",
            :items="colorByOptions",
            solo,
            :value="colorBy")
</template>

<script>
export default {
  props: {
    progress: {
      /* seconds */
      type: Number,
      required: true,
    },
    offset: {
      /* seconds */
      type: Number,
      default: 0,
    },
    length: {
      /* the total lenght of a clip in seconds */
      type: Number,
      default: 300,
    },
    colorBy: {
      type: String,
      required: true,
    },
    colorByOptions: {
      type: Array,
      required: true,
    },
    showLabels: {
      type: Boolean,
      default: true,
    },
    showShapes: {
      type: Boolean,
      default: true,
    },
    playing: {
      type: Boolean,
      default: true,
    },
    playbackRate: {
      type: Number,
      default: 1.0,
    },
    playbackRateOptions: {
      type: Array,
      default: () => [0.15, .25, 0.5, 1, 2, 5],
    },
  },
  data() {
    return {
      wasPlaying: this.playing,
      scrubbing: false,
    };
  },
  methods: {
    pad(num) {
      return num < 10 ? `0${num}` : num;
    },
    toTimeStamp(seconds) {
      const min = Math.floor(seconds / 60);
      const sec = Math.floor(seconds % 60);
      return `${this.pad(min)}:${this.pad(sec)}`;
    },
    progressChangeStart() {
      this.scrubbing = true;
      this.wasPlaying = this.playing;
      this.$emit("update:playing", false);
    },
    progressChangeEnd(e) {
      if (this.scrubbing) {
        this.scrubbing = false;
        this.$emit('update:playing', this.wasPlaying);
      } else {
        this.$emit('update:progress', e + this.offset);
      }
    },
    progressChangeInput(e) {
      if ((this.progress - this.offset) > this.length) {
        this.$emit("update:playing", false);
      }
      if ((this.progress - this.offset) < 0) {
        this.$emit('update:progress', this.offset + (Math.random() / 10));
      }
      if (this.scrubbing) {
        this.$emit('update:progress', e + this.offset);
      }
    },
  },
};
</script>

<style lang='stylus'>
.video-controls {

  .v-input {
    padding-top: 0;
  }
  .v-input--switch {
    padding-top: 0;
  }
  .v-input.v-text-field--solo .v-input__control {
    min-height: 1px;
    width: 140px;
  }
  .v-input__slot {
    margin: 0;
  }
}

.video-controls .v-toolbar__content {
  padding: 0 !important;
  height: inherit !important;
}
</style>
