<script>
import Raven from 'raven-js';

export default {
  props: {
    api: {
      type: Object,
      required: true,
    },
    instructions: {
      type: String,
      default: `If you experienced any problems 
        with this application or have suggestions
        for improvement, please let us know in the
        following text box.`
    },
  },
  data() {
    return {
      dialog: false,
      snackbar: false,
      feedback: '',
      error: '',
      submitDone: false,
    };
  },
  methods: {
    async submitFeedback() {
      Raven.setExtraContext({
        feedback: this.feedback,
      });
      try {
        await this.api.feedback(this.feedback);
      } catch(err) {
        this.error = err;
      } finally {
        this.feedback = '';
        this.snackbar = true;
        this.dialog = false;
      }
    },
  },
};
</script>

<template lang="pug">
div.stumpf-feedback-dialog
  v-dialog(
      v-model="dialog",
      @keydown.esc.stop="dialog = false",
      width="500px")
    v-card
      v-card-title.grey.lighten-3.title(dark, primary-title) Application Feedback
      v-card-text.pa-3
        v-flex
          p {{ instructions }}
        v-flex
          v-textarea.feedback(
            v-model="feedback",
            @keyup.stop.prevent="",
            box,
            auto-grow,
            clearable,
            full-width,
            tabindex="0")
          v-divider
      v-card-actions.pa-3
        v-spacer
        v-btn(@click="dialog = false") Close
        v-btn(
          @click="submitFeedback"
          :disabled="!feedback"
          color="primary"
          tabindex="0") Submit
    v-btn(slot="activator" icon @click="dialog = true" title="Submit feedback about the assignment")
      v-icon feedback
    
  v-snackbar(
      v-model="snackbar",
      bottom=false,
      left=false,
      top=true,
      right=false,
      :timeout="3000")
    p Feedback successfully submitted
    v-btn(flat @click="snackbar = false")
</template>

<style lang="stylus" scoped>
.stumpf-feedback-dialog
  align-self center
  .v-dialog__container
    align-self center !important
</style>
