<template lang="pug">
span.ban-button
  v-dialog(v-model="dialog", width="400")
    v-btn(
        slot="activator",
        :disabled="!workerId || loading"
        :color="banned ? 'success' : 'error'",
      ) {{ banned ? 'Unban' : 'Ban' }}
    v-card
      v-card-title.headline Really {{ banned ? 'Unban' : 'Ban' }} {{ workerId }}?
      v-card-actions
        v-spacer
        v-btn(flat, @click="dialog=false;") Cancel
        v-btn(
            @click="ban(!banned)",
            :color="banned ? 'success' : 'error'") {{ banned ? 'Unban' : 'Ban' }}
  v-snackbar(
      v-model="snackbar",
      :timeout="timeout",
      :top="top") {{ notification }}
    v-btn(color="primary", flat, @click="snackbar = false") Close
</template>

<script>
export default {
  props: {
    workerId: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      dialog: false,
      snackbar: false,
      timeout: 4000,
      top: true,
      notification: '',
      refresh: 0,
      loading: false,
    };
  },
  asyncComputed: {
    banned: {
      async get() {
        this.loading = false;
        if (this.workerId) {
          try {
            const { data: trash } = await this.axios.get(`worker/${this.workerId}`);
            console.log(trash);
            const { data } = await this.axios.get(`worker/${this.workerId}/ban`);
            return data.banned;
          } catch {
            return false;
          }
        }
        return false;
      },
      watch() { return this.refresh; },
    },
  },
  methods: {
    async ban(val) {
      try {
        this.loading = true;
        await this.axios.put(`worker/${this.workerId}/ban`, {
          ban: val,
        });
        this.notification = `Worker ${this.workerId} ban status set to ban=${val}`;
        setTimeout(() => { this.refresh += 1; }, 300);
      } catch (err) {
        this.notification = `Setting status to ban=${val} failed.`;
        this.loading = false;
      }
      this.snackbar = true;
      this.dialog = false;
    },
  },
}
</script>
