<script>
export default {
  props: {
    // Array[String]
    paragraphs: {
      type: Array,
      required: true,
    },
    // Array[{`keys`: Array[char], `desc`: String}]
    shortcuts: {
      type: Object,
      required: true,
    },
    forceShowOnce: {
      type: Boolean,
      required: true,
    },
  },
  data() {
    return {
      dialog: false,
    };
  },
  mounted() {
    try {
      if (!(window.localStorage.getItem('reid-help'))) {
        this.dialog = true
        window.localStorage.setItem('reid-help', true)
      }
    } catch (err) {
      console.err(err)
    }
  },
};
</script>

<template lang="pug">
v-dialog.stumpf-help-dialog(
    width="500px",
    v-model="dialog",
    @keydown.esc="dialog = false"
    @keydown.enter.stop="dialog = false")
  v-card
    v-card-title.grey.lighten-3.title(primary-title) Application Help
    v-card-text.pa-3
      v-flex
        v-subheader Instructions 
        v-flex(px-4)
          p(v-for="(paragraph, index) in paragraphs" :key="`help-${index}`") {{ paragraph }}
        v-subheader Keyboard shortcuts
        v-flex(px-4)
          table
            thead
              tr
                th Key
                th Description
            tbody
              tr(v-for="(value, key, index) in shortcuts", :key="`key-${index}`")  
                td.align-right
                  v-chip(small)
                    v-icon.pa-0(v-if="value.icon") {{ key }}
                    span(v-else) {{ key }}
                td {{ value.description }}
    v-card-actions.pa-3
      v-spacer
      v-btn(@click="dialog = false" color="primary") Okay (enter)
  v-btn(slot="activator" icon @click="dialog = true" title="Help and key bindings")
    v-icon help
</template>

<style lang="stylus" scoped>
.v-dialog__container
  align-self center !important
</style>
